<script>
  import { logOut } from '$lib/actions/auth.js';

  async function handleLogout(event) {
    event.preventDefault(); // tránh nhảy lên đầu trang
    await logOut();
  }

  let isDragging = false;
  let offset = { x: 0, y: 0 };
  let buttonEl;
  let showMobileMenu = false;
  let buttonStyle = {
    left: 'auto',
    top: 'auto',
    right: '1rem',
    bottom: '5rem'
  };

  const toggleMenu = () => {
    showMobileMenu = !showMobileMenu;
  };

  const handleTouchStart = (e) => {
    isDragging = true;
    const touch = e.touches[0];
    offset.x = touch.clientX - buttonEl.getBoundingClientRect().left;
    offset.y = touch.clientY - buttonEl.getBoundingClientRect().top;
  };

  const handleTouchMove = (e) => {
    if (!isDragging) return;
    e.preventDefault(); // Ngăn không scroll khi drag
    const touch = e.touches[0];
    const x = touch.clientX - offset.x;
    const y = touch.clientY - offset.y;

    buttonStyle = {
      ...buttonStyle,
      left: `${x}px`,
      top: `${y}px`,
      right: 'auto',
      bottom: 'auto'
    };
  };

  const handleTouchEnd = () => {
    if (!isDragging) return;
    isDragging = false;

    const screenWidth = window.innerWidth;
    const buttonRect = buttonEl.getBoundingClientRect();
    const buttonCenter = buttonRect.left + buttonRect.width / 2;

    if (buttonCenter < screenWidth / 2) {
      buttonStyle = {
        ...buttonStyle,
        left: '1rem',
        right: 'auto'
      };
    } else {
      buttonStyle = {
        ...buttonStyle,
        left: 'auto',
        right: '1rem'
      };
    }
  };
</script>

<div class="flex flex-col min-h-screen bg-gray-100 relative overflow-hidden">
  <!-- Header -->
  <header class="bg-white shadow-md p-4 flex items-center justify-between">
    <!-- Logo -->
    <div class="flex items-center space-x-2">
      <img src="https://dummyimage.com/40x40/000/fff.png&text=L" alt="Logo" class="w-10 h-10 rounded-full" />
      <span class="text-xl font-bold">MyApp</span>
    </div>

    <!-- Menu button (Mobile) -->
    <button on:click={toggleMenu} class="md:hidden text-gray-600 focus:outline-none">
      <svg xmlns="http://www.w3.org/2000/svg" class="w-8 h-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 8h16M4 16h16" />
      </svg>
    </button>

    <!-- Navigation Menu (Desktop) -->
    <nav class="hidden md:flex space-x-6">
      <a href="#" class="text-gray-600 hover:text-blue-600 font-medium">Home</a>
      <a href="#" class="text-gray-600 hover:text-blue-600 font-medium">About</a>
      <a href="#" class="text-gray-600 hover:text-blue-600 font-medium">Services</a>
      <a href="#" class="text-gray-600 hover:text-blue-600 font-medium">Contact</a>
    </nav>
  </header>

  <!-- Mobile Menu (toggle) -->
  {#if showMobileMenu}
    <div class="md:hidden bg-white shadow-md p-4 space-y-2 transition-all duration-300 ease-in-out">
      <a href="#" class="block text-gray-700 font-medium">Home</a>
      <a href="#" class="block text-gray-700 font-medium">About</a>
      <a href="#" class="block text-gray-700 font-medium">Services</a>
      <a href="#" class="block text-gray-700 font-medium">Contact</a>
      <a href="#" class="block text-gray-700 font-medium" on:click|preventDefault={handleLogout}>Logout</a>
    </div>
  {/if}

  <!-- Main Content -->
  <main class="flex-1 p-6">
    <h1 class="text-3xl font-bold mb-4">Welcome to MyApp</h1>
    <p class="text-gray-700">This is your main content area. Adjust and customize it as needed!</p>
  </main>

  <!-- Tab Actions (Mobile Only) -->
  <nav class="fixed bottom-0 left-0 right-0 bg-white border-t md:hidden flex justify-around py-2 shadow-inner z-20">
    <button class="flex flex-col items-center text-gray-500 hover:text-blue-600 active:text-blue-700">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-6 w-6 mb-1"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M3 9l9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z" />
      </svg>
      <span class="text-xs">Home</span>
    </button>

    <button class="flex flex-col items-center text-gray-500 hover:text-blue-600 active:text-blue-700">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-6 w-6 mb-1"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M21 21l-6-6m2-5a7 7 0 1 0-14 0 7 7 0 0 0 14 0z" />
      </svg>
      <span class="text-xs">Search</span>
    </button>

    <button class="flex flex-col items-center text-gray-500 hover:text-blue-600 active:text-blue-700">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-6 w-6 mb-1"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M15 17h5l-1.405-1.405C18.21 14.79 18 13.918 18 13V8c0-3.31-2.69-6-6-6S6 4.69 6 8v5c0 .918-.21 1.79-.595 2.595L4 17h5m6 0v1a3 3 0 0 1-6 0v-1m6 0H9" />
      </svg>
      <span class="text-xs">Alerts</span>
    </button>

    <button class="flex flex-col items-center text-gray-500 hover:text-blue-600 active:text-blue-700">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-6 w-6 mb-1"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor">
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M5.121 17.804A13.937 13.937 0 0 1 12 15c2.757 0 5.304.837 7.879 2.25M15 10a3 3 0 1 1-6 0 3 3 0 0 1 6 0z" />
      </svg>
      <span class="text-xs">Profile</span>
    </button>
  </nav>

  <!-- Floating Create Button -->
  <!-- svelte-ignore a11y_consider_explicit_label -->
  <button
    bind:this={buttonEl}
    class="draggable fixed bottom-20 right-4 w-14 h-14 bg-blue-500 hover:bg-blue-600 active:bg-blue-700 rounded-full shadow-lg flex items-center justify-center text-white z-30"
    style={buttonStyle}
    on:touchstart={handleTouchStart}
    on:touchmove={handleTouchMove}
    on:touchend={handleTouchEnd}>
    <svg xmlns="http://www.w3.org/2000/svg" class="w-8 h-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 4v16m8-8H4" />
    </svg>
  </button>
</div>

<style>
  .draggable {
    touch-action: none;
  }
</style>
